import React from "react";
import { Router } from "@reach/router";
import Home from "./pages/Home";
import Details from "./pages/Details";
import NotFound from "./pages/NotFound";

function App() {
  return (
    <main>
      <header>
        <a href="/cari-karya-seni/">
          <h2>Cari Karya Seni</h2>
        </a>
      </header>
      <Router>
        <Home path="/cari-karya-seni/" exact />
        <Details path="/cari-karya-seni/details/:id" />
        <NotFound default />
      </Router>
    </main>
  );
}

export default App;
